import PropTypes from "prop-types";
import React from "react";
import { setActiveSort } from "../../helpers/product";

const ShopStates = ({ states, getSortParams }) => {
  return (
    <div className="sidebar-widget">
      <h4 className="pro-sidebar-title">State </h4>
      <div className="sidebar-widget-list mt-30">
        {states ? (
          <ul>
            <li>
              <div className="sidebar-widget-list-left">
                <button
                  onClick={(e) => {
                    getSortParams("state", "");
                    setActiveSort(e);
                  }}
                >
                  <span className="checkmark" /> All States
                </button>
              </div>
            </li>
            {states.map((state, key) => {
              return (
                <li key={key}>
                  <div className="sidebar-widget-list-left">
                    <button
                      onClick={(e) => {
                        getSortParams("state", state);
                        setActiveSort(e);
                      }}
                    >
                      {" "}
                      <span className="checkmark" /> {state}{" "}
                    </button>
                  </div>
                </li>
              );
            })}
          </ul>
        ) : (
          "No categories found"
        )}
      </div>
    </div>
  );
};

ShopStates.propTypes = {
  categories: PropTypes.array,
  getSortParams: PropTypes.func,
};

export default ShopStates;
