import PropTypes from "prop-types";
import React, { Fragment, useState } from "react";
import { Link } from "react-router-dom";
import { useToasts } from "react-toast-notifications";
import Rating from "./sub-components/ProductRating";
import ProductModal from "./ProductModal";

const ShopGridListSingle = ({
  shop,
  currency,
  addToCart,
  addToWishlist,
  addToCompare,
  cartItem,
  wishlistItem,
  compareItem,
  sliderClassName,
  spaceBottomClass,
}) => {
  const [modalShow, setModalShow] = useState(false);
  const { addToast } = useToasts();
  return (
    <Fragment>
      <div
        className={`col-xl-4 col-sm-6 ${
          sliderClassName ? sliderClassName : ""
        }`}
      >
        <div
          className={`product-wrap ${spaceBottomClass ? spaceBottomClass : ""}`}
        >
          <div className="product-img">
            <Link to={process.env.PUBLIC_URL + "/shop/" + shop.id}>
              <img
                width={200}
                height={200}
                className="default-img"
                src="/assets/img/logo/shop_placeholder.jpeg"
                alt=""
              />
            </Link>
            {shop.discount || shop.new ? (
              <div className="shop-img-badges">
                {shop.discount ? (
                  <span className="pink">-{shop.discount}%</span>
                ) : (
                  ""
                )}
                {shop.new ? <span className="purple">New</span> : ""}
              </div>
            ) : (
              ""
            )}

            <div className="product-action">
              <div className="pro-same-action pro-wishlist">
                <button
                  className={wishlistItem !== undefined ? "active" : ""}
                  disabled={wishlistItem !== undefined}
                  title={
                    wishlistItem !== undefined
                      ? "Added to wishlist"
                      : "Add to wishlist"
                  }
                  onClick={() => addToWishlist(shop, addToast)}
                >
                  <i className="pe-7s-like" />
                </button>
              </div>
              <div className="pro-same-action pro-cart">
                {shop.affiliateLink ? (
                  <a
                    href={shop.affiliateLink}
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    {" "}
                    Buy now{" "}
                  </a>
                ) : shop.variation && shop.variation.length >= 1 ? (
                  <Link to={`${process.env.PUBLIC_URL}/shop/${shop.id}`}>
                    Select Option
                  </Link>
                ) : shop.stock && shop.stock > 0 ? (
                  <button
                    onClick={() => addToCart(shop, addToast)}
                    className={
                      cartItem !== undefined && cartItem.quantity > 0
                        ? "active"
                        : ""
                    }
                    disabled={cartItem !== undefined && cartItem.quantity > 0}
                    title={
                      cartItem !== undefined ? "Added to cart" : "Add to cart"
                    }
                  >
                    {" "}
                    <i className="pe-7s-cart"></i>{" "}
                    {cartItem !== undefined && cartItem.quantity > 0
                      ? "Added"
                      : "Add to cart"}
                  </button>
                ) : (
                  <button disabled className="active">
                    Out of Stock
                  </button>
                )}
              </div>
              <div className="pro-same-action pro-quickview">
                <button onClick={() => setModalShow(true)} title="Quick View">
                  <i className="pe-7s-look" />
                </button>
              </div>
            </div>
          </div>
          <div className="shop-content text-center">
            <h3>
              <Link to={process.env.PUBLIC_URL + "/shop/" + shop.id}>
                {shop.name} <br />
                <h5>
                  {shop.city}, {shop.state}
                </h5>
              </Link>
            </h3>
            {shop.rating && shop.rating > 0 ? (
              <div className="product-rating">
                <Rating ratingValue={shop.rating} />
              </div>
            ) : (
              ""
            )}
          </div>
        </div>
        <div className="shop-list-wrap mb-30">
          <div className="row">
            <div className="col-xl-4 col-md-5 col-sm-6">
              <div className="product-list-image-wrap">
                <div className="product-img">
                  <Link to={process.env.PUBLIC_URL + "/shop/" + shop.id}>
                    <img
                      className="default-img img-fluid"
                      src={"/assets/img/logo/shop_placeholder.jpeg"}
                      alt=""
                    /> 
                    {shop.imageSet && shop.imageSet.length > 1 ? (
                      <img
                        className="hover-img img-fluid"
                        src={process.env.PUBLIC_URL + shop.imageSet[1]}
                        alt=""
                      />
                    ) : (
                      ""
                    )}
                  </Link> 
                  {shop.discount || shop.new ? (
                    <div className="product-img-badges">
                      {shop.discount ? (
                        <span className="pink">-{shop.discount}%</span>
                      ) : (
                        ""
                      )}
                      {shop.new ? <span className="purple">New</span> : ""}
                    </div>
                  ) : (
                    ""
                  )}
                </div>
              </div>
            </div>
            <div className="col-xl-8 col-md-7 col-sm-6">
              <div className="shop-list-content">
                <h3>
                  <Link to={process.env.PUBLIC_URL + "/shop/" + shop.id}>
                    {shop.name}
                    {shop.city}, {shop.state}
                  </Link>
                </h3>

                {shop.rating && shop.rating > 0 ? (
                  <div className="rating-review">
                    <div className="shop-list-rating">
                      <Rating ratingValue={shop.rating} />
                    </div>
                  </div>
                ) : (
                  ""
                )}
                {shop.shortDescription ? <p>{shop.shortDescription}</p> : ""}

                {/* <div className="shop-list-actions d-flex align-items-center">
                  <div className="shop-list-btn btn-hover">
                    {shop.affiliateLink ? (
                      <a
                        href={shop.affiliateLink}
                        rel="noopener noreferrer"
                        target="_blank"
                      >
                        {" "}
                        Buy now{" "}
                      </a>
                    ) : shop.variation && shop.variation.length >= 1 ? (
                      <Link to={`${process.env.PUBLIC_URL}/shop/${shop.id}`}>
                        Select Option
                      </Link>
                    ) : shop.stock && shop.stock > 0 ? (
                      <button
                        onClick={() => addToCart(shop, addToast)}
                        className={
                          cartItem !== undefined && cartItem.quantity > 0
                            ? "active"
                            : ""
                        }
                        disabled={
                          cartItem !== undefined && cartItem.quantity > 0
                        }
                        title={
                          cartItem !== undefined
                            ? "Added to cart"
                            : "Add to cart"
                        }
                      >
                        {" "}
                        <i className="pe-7s-cart"></i>{" "}
                        {cartItem !== undefined && cartItem.quantity > 0
                          ? "Added"
                          : "Add to cart"}
                      </button>
                    ) : (
                      <button disabled className="active">
                        Out of Stock
                      </button>
                    )}
                  </div>

                  <div className="shop-list-wishlist ml-10">
                    <button
                      className={wishlistItem !== undefined ? "active" : ""}
                      disabled={wishlistItem !== undefined}
                      title={
                        wishlistItem !== undefined
                          ? "Added to wishlist"
                          : "Add to wishlist"
                      }
                      onClick={() => addToWishlist(shop, addToast)}
                    >
                      <i className="pe-7s-like" />
                    </button>
                  </div>
                  <div className="shop-list-compare ml-10">
                    <button
                      className={compareItem !== undefined ? "active" : ""}
                      disabled={compareItem !== undefined}
                      title={
                        compareItem !== undefined
                          ? "Added to compare"
                          : "Add to compare"
                      }
                      onClick={() => addToCompare(shop, addToast)}
                    >
                      <i className="pe-7s-shuffle" />
                    </button>
                  </div>
                </div> */}
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* shop modal */}
      {/* <ProductModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        product={shop}
        currency={currency}
        cartitem={cartItem}
        wishlistitem={wishlistItem}
        compareitem={compareItem}
        addtocart={addToCart}
        addtowishlist={addToWishlist}
        addtocompare={addToCompare}
        addtoast={addToast}
      /> */}
    </Fragment>
  );
};

ShopGridListSingle.propTypes = {
  addToCart: PropTypes.func,
  addToCompare: PropTypes.func,
  addToWishlist: PropTypes.func,
  cartItem: PropTypes.object,
  compareItem: PropTypes.object,
  currency: PropTypes.object,
  shop: PropTypes.object,
  sliderClassName: PropTypes.string,
  spaceBottomClass: PropTypes.string,
  wishlistItem: PropTypes.object,
};

export default ShopGridListSingle;
