import React from "react";
import { MapContainer, TileLayer, Marker, Popup, useMap } from 'react-leaflet'
import { Link } from "react-router-dom";
import { MarkerIcon } from './Icons';
import 'leaflet/dist/leaflet.css';

function ChangeView({ center, zoom }) {
    const map = useMap();
    map.setView(center, zoom);
    return null;
}

const LeafletMapLayout = ({ center, zoom, mapHeight, markers }) => {
    const mapStyles = {
        width: "100%",
        height: mapHeight || 400, // default map heigth
    };
    return (
        <MapContainer center={{ lat: center.lat || 51.505, lon: center.lon || -0.09 }} zoom={zoom || 6} scrollWheelZoom={false} style={mapStyles}>
            <ChangeView center={{ lat: center.lat || 51.505, lon: center.lon || -0.09 }} zoom={zoom || 6}/> 
            <TileLayer
                attribution=''
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            {markers.map((marker,index) => (
                <Marker key={index} position={[marker.lat, marker.lon]} icon={MarkerIcon}>
                    <Popup>
                    <Link to={marker.url}>
                        <span>{marker.name}</span>
                    </Link>
                    </Popup>
                </Marker>
            ))}

        </MapContainer>

    )
}

export default LeafletMapLayout;