import PropTypes from "prop-types";
import React from "react";
import {
  getIndividualStates,
  getIndividualTags,
  getIndividualColors,
  getProductsIndividualSizes,
} from "../../helpers/shops";
import ShopSearch from "../../components/product/ShopSearch";
import ShopStates from "../../components/product/ShopStates";
import ShopColor from "../../components/product/ShopColor";
import ShopSize from "../../components/product/ShopSize";
import ShopTag from "../../components/product/ShopTag";

const ShopSidebar = ({ shops, getSortParams, sideSpaceClass }) => {
  // const uniqueStates = getIndividualStates(shops);
  const uniqueColors = getIndividualColors(shops);
  const uniqueSizes = getProductsIndividualSizes(shops);
  const uniqueTags = getIndividualTags(shops);
  const uniqueStates = getIndividualStates(shops);

  return (
    <div className={`sidebar-style ${sideSpaceClass ? sideSpaceClass : ""}`}>
      {/* shop search */}
      <ShopSearch />

      {/* filter by categories */}
      <ShopStates states={uniqueStates} getSortParams={getSortParams} />

      {/* filter by color */}
      <ShopColor colors={uniqueColors} getSortParams={getSortParams} />

      {/* filter by size */}
      <ShopSize sizes={uniqueSizes} getSortParams={getSortParams} />

      {/* filter by tag */}
      <ShopTag tags={uniqueTags} getSortParams={getSortParams} />
    </div>
  );
};

ShopSidebar.propTypes = {
  getSortParams: PropTypes.func,
  shops: PropTypes.array,
  sideSpaceClass: PropTypes.string,
};

export default ShopSidebar;
