import PropTypes from "prop-types";
import React, {useEffect, useState} from "react";
import ShopGridList from "./ShopGridList";
import LeafletMapLayout from '../../components/leaflet-map-layout/LeafletMapLayout';

const ShopListings = ({ shops, layout }) => {
  const [mapMarkers, setMapMarkers] = useState([]);
  useEffect(() => {
    if (layout === 'map') {
      const markers = shops.map(shop => {
        return { 
          name: shop.name, 
          lon: shop.longitude, 
          lat: shop.latitude, 
          url: `${process.env.PUBLIC_URL}/shop/${shop.id}`
        };
      })
        .filter(el => el);
      setMapMarkers(markers);
    }
  }, [shops, layout]);

  return (
    <div className="shop-bottom-area mt-35">
      {layout === 'map' ? (
        <LeafletMapLayout mapHeight="70vh" markers={mapMarkers} center={{ lon: mapMarkers && mapMarkers[0] ? mapMarkers[0].lon : '', lat: mapMarkers && mapMarkers[0] ? mapMarkers[0].lat : '' }} />
      ) : (
      <div className={`row ${layout ? layout : ""}`}>
        <ShopGridList shops={shops} spaceBottomClass="mb-25" />
      </div>
      )}
    </div>
  );
};

ShopListings.propTypes = {
  layout: PropTypes.string,
  shops: PropTypes.array,
};

export default ShopListings;
