//get products based on category
export const getSortedShops = (shops, sortType, sortValue) => {
  if (shops && sortType && sortValue) {
    if (sortType === "state") {
      return shops.filter((single) => single.state === sortValue);
    }
    if (sortType === "city") {
      return shops.filter(
        (shop) => shop.tag.filter((single) => single.city === sortValue)[0]
      );
    }
    if (sortType === "logitude") {
      return shops.filter((shop) => shop.color === sortValue);
    }
    if (sortType === "size") {
      return shops.filter((shop) => shop.size === sortValue);
    }
    if (sortType === "filterSort") {
      let sortShops = [...shops];
      if (sortValue === "default") {
        return sortShops;
      }
      if (sortValue === "priceHighToLow") {
        return sortShops.sort((a, b) => {
          return b.price - a.price;
        });
      }
      if (sortValue === "priceLowToHigh") {
        return sortShops.sort((a, b) => {
          return a.price - b.price;
        });
      }
    }
  }
  return shops;
};

// get individual element
const getIndividualItemArray = (array) => {
  let individualItemArray = array.filter(function (v, i, self) {
    return i === self.indexOf(v);
  });
  return individualItemArray;
};

// get individual categories
export const getIndividualStates = (shops) => {
  let shopStates = [];
  shops &&
    shops.map((single) => {
      // console.log(single.state);
      return shopStates.push(single.state);
    });
  const individualShopStates = getIndividualItemArray(shopStates);
  return individualShopStates;
};

// get individual tags
export const getIndividualTags = (products) => {
  let productTags = [];
  products &&
    products.map((product) => {
      return (
        product.tags &&
        product.tags.map((single) => {
          return productTags.push(single);
        })
      );
    });
  const individualProductTags = getIndividualItemArray(productTags);
  return individualProductTags;
};

// get individual colors
export const getIndividualColors = (products) => {
  let productColors = [];
  products &&
    products.map((product) => {
      return productColors.push(product.color);
    });
  const individualProductColors = getIndividualItemArray(productColors);
  return individualProductColors;
};

// get individual sizes
export const getProductsIndividualSizes = (products) => {
  let productSizes = [];
  products &&
    products.map((product) => {
      return productSizes.push(product.size);
    });
  const individualProductSizes = getIndividualItemArray(productSizes);
  return individualProductSizes;
};

// get product individual sizes
export const getIndividualSizes = (product) => {
  let productSizes = [];
  product.variation &&
    product.variation.map((singleVariation) => {
      return (
        singleVariation.size &&
        singleVariation.size.map((singleSize) => {
          return productSizes.push(singleSize.name);
        })
      );
    });
  const individualSizes = getIndividualItemArray(productSizes);
  return individualSizes;
};

export const setActiveSort = (e) => {
  const filterButtons = document.querySelectorAll(
    ".sidebar-widget-list-left button, .sidebar-widget-tag button, .product-filter button"
  );
  filterButtons.forEach((item) => {
    item.classList.remove("active");
  });
  e.currentTarget.classList.add("active");
};

export const setActiveLayout = (e) => {
  const gridSwitchBtn = document.querySelectorAll(".shop-tab button");
  gridSwitchBtn.forEach((item) => {
    item.classList.remove("active");
  });
  e.currentTarget.classList.add("active");
};

export const toggleShopTopFilter = (e) => {
  const shopTopFilterWrapper = document.querySelector(
    "#product-filter-wrapper"
  );
  shopTopFilterWrapper.classList.toggle("active");
  if (shopTopFilterWrapper.style.height) {
    shopTopFilterWrapper.style.height = null;
  } else {
    shopTopFilterWrapper.style.height =
      shopTopFilterWrapper.scrollHeight + "px";
  }
  e.currentTarget.classList.toggle("active");
};
